import { Component, OnInit } from '@angular/core';
import {StartiService} from '../shared/starti.service';
import {Starti} from '../shared/starti.model';
import {subscribeOn} from 'rxjs/operators';
@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {employeeList: Starti[];

  constructor(private startiService: StartiService) { }

  ngOnInit() {
const x = this.startiService.getData();
x.snapshotChanges().subscribe(item => {
  this.employeeList = [];
  item.forEach(element => {
    const y = element.payload.toJSON();
    y['$key'] = element.key;
    this.employeeList.push(y as Starti);
  });
});
  }

}
