       import { NgModule } from '@angular/core' ;
import {Routes, RouterModule } from '@angular/router' ;
       import {StartiComponent} from './starti/starti.component';
       import {SearchiComponent} from './searchi/searchi.component';
       import {PayiComponent} from './payi/payi.component';
      import {Des11Component} from './des11/des11.component';
      import {Des13Component} from './des13/des13.component';
        import {Des18Component} from './des18/des18.component';
       import {Des19Component} from './des19/des19.component';
       import {Des25Component} from './des25/des25.component';
       import {Des26Component} from './des26/des26.component';
       import {Des27Component} from './des27/des27.component';
       import {Des28Component} from './des28/des28.component';
       import {Des29Component} from './des29/des29.component';

       const routes: Routes = [
  {path: 'payment' , component:  PayiComponent},
  {path: 'search' , component: SearchiComponent},
  {path: 'start' , component:  StartiComponent},


         {path: 'des11' , component:  Des11Component},


         {path: 'des13' , component:  Des13Component},
          {path: 'des18' , component:  Des18Component},
         {path: 'des19' , component:  Des19Component},

          {path: 'des25' , component:  Des25Component},
         {path: 'des26' , component:  Des26Component},
         {path: 'des27' , component:  Des27Component},
         {path: 'des28' , component:  Des28Component},
         {path: 'des29' , component:  Des29Component}

       ];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
       export class AppRoutingModule { }
 export  const routingComponents = [ PayiComponent , SearchiComponent , StartiComponent , Des11Component, Des13Component,  Des18Component, Des19Component,  Des25Component, Des26Component, Des27Component, Des28Component, Des29Component];
