import { Component, OnInit } from '@angular/core';
import {Starti} from '../shared/starti.model';
import {NgForm} from '@angular/forms';
import {User} from '../user';
import {Dewa} from '../dewa';
import {StartiService} from '../shared/starti.service';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-des11',
  templateUrl: './des11.component.html',
  styleUrls: ['./des11.component.css']
})
export class Des11Component implements OnInit {

  employeeList: Starti[];
  dew = new Dewa( ' ' , '', '', '' );

  userModel = new User('', '',   '', 201008, '', '', 9873894021);
  constructor(private startiService: StartiService, private toastr: ToastrService) { }

  ngOnInit() {
    const x = this.startiService.getData();
    x.snapshotChanges().subscribe(item => {
      this.employeeList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['$key'] = element.key;
        this.employeeList.push(y as Starti);
      });
    });
  }
  onSubmit(employeeForm: NgForm) {
    this.toastr.success('Order Confirmed', 'Order Summary');
  }

}
