import { Injectable } from '@angular/core';
import {AngularFireDatabase, AngularFireList} from 'angularfire2/database';
import { Starti } from './starti.model';
@Injectable({
  providedIn: 'root'
})
export class StartiService {
employeeList: AngularFireList<any>;
selectedEmployee: Starti = new Starti();
  constructor(private  firebase: AngularFireDatabase) { }

getData() {
  this.employeeList = this.firebase.list('app');
  return this.employeeList;
}
insertEmployee(starti: Starti) {
    this.employeeList.push({
      email: starti.email,
  username: starti.username,
  name: starti.name,
  password: starti.password
    });
}

updateEmployee(starti: Starti) {
  this.employeeList.update(starti.$key,
    {
      email: starti.email,
      username: starti.username,
      name: starti.name,
      password: starti.password
    } );
}
deleteEmployee($key: string) {
  this.employeeList.remove($key);
}

}
